import Master from '../componentes/Master';
import fetch from 'isomorphic-unfetch';
import Precio from '../componentes/Precio';
import Noticias from '../componentes/Noticias';

const Index = (props) => (
    <Master>
        <div className="row">
            <div className="col-12">
                <h2>Precio del Bitcoin</h2>
                <Precio precio={props.precioBitcoin}/>
            </div>
            <div className="col-md-8">
                <h2>Noticias sobre Bitcoin</h2>
                <Noticias noticias={props.noticias}/>
                {console.log(props.noticias)}
            </div>
            <div className="col-md-4">
                <h2>Pr√≥ximos Eventos Bitcoin</h2> 
            </div>
        </div>
    </Master>
);

Index.getInitialProps = async () => {
    const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
    const noticas = await fetch('https://newsapi.org/v2/everything?q=bitcoin&sortBy=publishedAt&apiKey=bb7f731971db45a088b78983e25e4103&languaje=es');
    const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Mexico&token=BY6SXWKXQGIN7TL7F3HW');

    const resPrecio = await precio.json();
    const resNoticas = await noticas.json();
    const resEventos = await eventos.json();

    return {
        precioBitcoin: resPrecio.data.quotes.USD,
        noticias : resNoticas.articles,
        eventos : resEventos.events
    }
}

export default Index;